<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">被投企业账号管理</div>
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getListData(1)">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
        <el-date-picker v-model="selectTime" type="date" :editable='false' placeholder="选择日期" :clearable='true' value-format="yyyy-MM-dd" @change="getListData(1)" class="header-right-time right">
        </el-date-picker>
      </div>
      <div class="historyapply-group">
        <div class="clear table-group-title">
          <div class="left breadcrumb">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/home/investProjectManageHomepage' ,query: {id:projectId}}" v-if="type==2">返回项目首页</el-breadcrumb-item>
              <el-breadcrumb-item>被投企业账号管理（{{total}}）</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="line"></div>
        </div>
        <div class="historyapply-group-tablebox">
          <div class="table-normal-box">
            <table class="table-normal">
              <tr>
                <th width="30">序号</th>
                <th>公司名称</th>
                <th>分配人员</th>
                <th>分配账号</th>
                <th>分配账号时间</th>
                <th width="160">操作</th>
              </tr>
              <tr v-for="item in list" :key="item.seq">
                <td>{{item.seq}}</td>
                <td>{{item.companyName}}</td>
                <td v-if="item.account==''||item.account==null">未分配</td>
                <td v-else>{{item.name}}</td>
                <td v-if="item.account==''||item.account==null">未分配</td>
                <td v-else>{{item.account}}</td>
                <td v-if="item.account==''||item.account==null">未分配</td>
                <td v-else>{{item.add_time}}</td>
                <td v-if="item.account==''||item.account==null" class="creditCz-box" @click="alterAdd(item.cid)"><span class="table-link">分配账号</span>
                </td>
                <td v-else class="creditCz-box">
                  <span class="table-link" v-if="item.allow==0" @click="toStarOrForbidAcc(item.id,1)">启用</span>
                  <span class="table-link" v-if="item.allow==1" @click="toStarOrForbidAcc(item.id,0)">停用</span>
                  <span class="table-link" @click="alterEditor(item.id,item.name,item.phone,item.account)">编辑</span>
                  <span class="table-link" @click="toDelAccount(item.id)">删除账户</span>
                </td>
              </tr>
            </table>
            <div class="table-nodata" v-if="total==0">
              <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
          </div>
          <div class="pagination-normal" v-if="total!=0">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
      <el-dialog :title="alertTitle" :visible.sync="fileuploadDialog" width="570px" center :append-to-body='true' :before-close="toCancleConfirm">
        <div class="dialog_content">
          <div class="form-group margin-bottom-none">
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>姓名</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-input v-model="name" placeholder="请填写" :clearable='true' @change="inputChange" class="header-right-time right"></el-input>
                </div>
                <span class="form-group-inputbox-error" v-if="nameReg">{{nameErrorMsg}}</span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear  " :class="{'margin-bottom-none':!showUserAndPassoword}">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>手机号</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-input v-model="phone" placeholder="请填写" :clearable='true' @change="inputChange" class="header-right-time right" maxlength='11'></el-input>
                  <!-- <input class="form-group-inputbox-inputtext" type="text" v-model="phone" @change="phoneChange" > -->
                </div>
                <span class="form-group-inputbox-error" v-if="phoneReg">{{phoneErrorMsg}}</span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear" v-if="showUserAndPassoword">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title">账号</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-input v-model="userName" placeholder="若不填写则默认账号为手机号" :clearable='true' @change="inputChange" class="header-right-time right"></el-input>
                </div>
                <!-- <span class="form-group-inputbox-error">{{userNameErrorMsg}}</span> -->
              </div>
            </div>
            <div class="form-group-inputboxgroup clear margin-bottom-none" v-if="showUserAndPassoword">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title">密码</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-input v-model="password" :placeholder="alertType=='add'?'若不填写则默认密码为手机号':'******'" :clearable='true' @change="inputChange" class="header-right-time right"></el-input>
                </div>
                <!-- <span class="form-group-inputbox-error">{{passwordErrorMsg}}</span> -->
              </div>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" v-if="alertType=='add'" @click="toAddAccount">添加</button>
          <button class="dialog-btn-submit btn-blue" v-if="alertType=='editor'" @click="toEditAccount">修改</button>
          <button class="dialog-btn-submit btn-cancle" @click="toCancleConfirm">取消</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  accountList,
  starOrForbidAcc,
  delAccount,
  addAccount,
  editAccount
} from '../../config/api'
import {
  dialogConfirmHasbtn,
  dialogConfirm
} from '../../config/mUtils'
export default {
  data() {
    return {
      list: [],
      pageSize: 10,
      projectId: '',
      type: 1, //1=>1级路由，2=>2级路由
      selectTime: null,
      keyWord: null,
      total: null,
      currentPage: 1,
      fileuploadDialog: false,
      id: '', //存放
      name: '', //弹窗中名字的值
      phone: '', //弹窗中手机号码的值
      userName: '', //弹窗用户名
      password: '', //弹窗密码
      alertType: 'add', //打开弹窗类型
      alertTitle: '添加账号', //弹窗头部信息
      nameErrorMsg: '', //姓名错误信息提示
      phoneErrorMsg: '', //手机号错误信息提示
      userNameErrorMsg: '', //用户错误信息提示
      passwordErrorMsg: '', //密码错误信息提示
      canConfirm: true,
      canSave: false,
      showUserAndPassoword: true, //是否显示密码和错误信息
      notConfim: true,
    }
  },
  computed: {
    //基本信息验证
    nameReg: function() { //姓名
      if (this.notConfim) {
        return false
      }
      if (this.name == '' || this.name == undefined) {
        this.nameErrorMsg = "*姓名不能为空";
        return true;
      } else {
        return false;
      }
    },
    phoneReg: function() { //手机号码
      if (this.notConfim) {
        return false
      }
      let phoneTest = /^1[34578]\d{9}$/;
      if (this.phone == '' || this.phone == undefined) {
        this.phoneErrorMsg = "*手机号不能为空"
        return true;
      } else {
        if (!phoneTest.test(this.phone)) {
          this.phoneErrorMsg = "*手机号码格式不正确"
          return true;
        } else {
          // this.userName = JSON.parse(JSON.stringify(this.phone));
          // this.password = JSON.parse(JSON.stringify(this.phone));
          return false;
        }
      }
    },
    // userNameReg: function() { //用户名
    //   if (this.notConfim) {
    //     return false
    //   }
    //   if (this.userName == '' || this.userName == undefined) {
    //     this.userNameErrormsg = "*用户名不能为空";
    //     return true;
    //   } else {
    //     return false;
    //   }
    // },
    // passwordReg: function() { //密码
    //   if (this.notConfim) {
    //     return false
    //   }
    //   if (this.password == '' || this.password == undefined) {
    //     this.passwordErrormsg = "*密码不能为空";
    //     return true;
    //   } else {
    //     return false;
    //   }
    // },
  },
  watch: {
    $route(to, from) {
      this.initData();
    }
  },
  mounted() {
    this.initData();
  },
  methods: {
    initData() {
      if (this.$route.query.projectId != undefined) {
        this.projectId = this.$route.query.projectId;
        this.type = 2;
      } else {
        this.type = 1;
        this.projectId = ''
      }
      this.getListData(1);
    },
    //改变搜索的关键字
    // isChange() {
    //   this.list = [];
    //   this.getListData();
    // },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.getListData(val)
      }
    },
    //获取数据
    getListData(val) {
      this.list = [];
      this.currentPage = val;
      let params = {
        page: val,
        number: this.pageSize,
      }
      // console.log(params);
      // console.log(this.selectTime);
      if (this.selectTime != null && this.selectTime != '') {
        params.time = this.selectTime
      }
      if (this.keyWord != null && this.keyWord != '') {
        params.search = this.keyWord;
      }
      if (this.projectId != '') {
        params.projectId = this.projectId
      }
      accountList(params).then(res => {
        let data = res.data.payload;
        this.total = data.total;
        if (data.data.length != 0) {
          this.list = data.data;
        }
      })
    },

    //停用或启用账户
    toStarOrForbidAcc(id, type) {
      starOrForbidAcc({
        id: id,
        type: type
      }).then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          // this.getListData(this.currentPage);
          let str = '';
          if (type == 1) {
            str = '账户启用成功'
          } else {
            str = '账户停用成功'
          }
          dialogConfirm('success', str);
          for (let i of this.list) {
            if (i.id == id) {
              i.allow = type;
            }
          }
        }

      })
    },
    //删除账户
    toDelAccount(id) {
      dialogConfirmHasbtn('删除该账户将使此公司中所有账号包括子账号都将被删除，确定要删除该账户吗？').then(() => {
        delAccount({
          id: id,
        }).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            this.getListData(this.currentPage);
            dialogConfirm('success', '账号删除成功');
          }

        })
        // this.addaccountDialogVisible = false;
      }).catch(() => {});
    },
    inputChange() {
      this.canSave = true;
    },
    //弹出分配账户窗口时
    alterAdd(cid) {
      // console.log(cid);
      this.fileuploadDialog = true;
      this.alertType = 'add',
        this.alertTitle = '添加账号'
      this.id = cid;
      this.name = '';
      this.phone = '';
      this.userName = '';
      this.password = '';
      this.phoneErrorMsg = '';
      this.nameErrorMsg = '';
      this.notConfim = true;
    },

    //分配账户
    toAddAccount() {
      this.notConfim = false;
      // if (!this.nameReg && !this.phoneReg && !this.userNameReg && !this.passwordReg) {
      if (!this.nameReg && !this.phoneReg) {
        let newObj = {
          cid: this.id,
          name: this.name,
          phone: this.phone
        };
        if (this.userName != '' && this.userName != null && this.userName != undefined) {
          newObj.account = this.userName;
        }
        if (this.password != '' && this.password != null && this.password != undefined) {
          newObj.password = this.password;
        }
        // console.log(11111)
        addAccount(newObj).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            this.getListData(this.currentPage);
            dialogConfirm('success', '账号添加成功');
            this.fileuploadDialog = false;
            this.canSave = false;
          } else {
            dialogConfirm('info', res.data.message);
          }
        })
      }
      // this.phoneErrorMsg = '';
      // this.nameErrorMsg = '';
      // let phonereg = /^1[34578]\d{9}$/;
      // if (this.name == '') {
      //   this.nameErrorMsg = '*姓名不能为空'
      //   // return false;
      // }
      // if (this.phone == '') {
      //   this.phoneErrorMsg = '*手机号不能为空'
      //   return false;
      // } else {
      //   if (!phonereg.test(this.phone)) {
      //     this.phoneErrorMsg = '*手机号码格式错误'
      //     return false;
      //   }
      // }

    },

    //弹出编辑账户窗口时调用
    alterEditor(id, name, phone, account) {
      this.fileuploadDialog = true;
      this.alertType = 'editor';
      this.alertTitle = '修改账号信息';
      this.nameErrorMsg = '';
      this.phoneErrorMsg = '';
      this.name = name;
      this.phone = phone;
      this.userName = account;
      this.password = '';
      this.id = id;
      this.notConfim = true;
    },

    //编辑账户
    toEditAccount() {
      this.notConfim = false;
      // if (!this.nameReg && !this.phoneReg && !this.userNameReg && !this.passwordReg) {
      if (!this.nameReg && !this.phoneReg) {
        let newObj = {
          id: this.id,
          name: this.name,
          phone: this.phone
        };
        if (this.userName != '' && this.userName != null && this.userName != undefined) {
          console.log('userName')
          newObj.account = this.userName;
        }
        if (this.password != '' && this.password != null && this.password != undefined) {
          console.log('password')
          newObj.password = this.password;
        }
        console.log(newObj);
        editAccount(newObj).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            this.getListData(this.currentPage);
            dialogConfirm('success', '账户信息修改成功');
            this.fileuploadDialog = false;
            this.canSave = false;
          } else {
            dialogConfirm('info', res.data.message);
          }
        })
      }
      // this.phoneErrorMsg = '';
      // this.nameErrorMsg = '';
      // let phonereg = /^1[34578]\d{9}$/;
      // //console.log(this.name);
      // if (this.name == '') {
      //   this.nameErrorMsg = '*姓名不能为空'
      //   return false
      // }
      // if (this.phone == '') {
      //   this.phoneErrorMsg = '*手机号不能为空'
      //   return false
      // } else {
      //   if (!phonereg.test(this.phone)) {
      //     this.phoneErrorMsg = '*手机号码格式错误'
      //     return false
      //   }
      // }

    },

    //取消提交
    toCancleConfirm() {
      if (this.canSave) {
        dialogConfirmHasbtn('确定不保存退出么？').then(() => {
          this.canSave = false;
          this.fileuploadDialog = false;
        }).catch(() => {});
      } else {
        this.fileuploadDialog = false;
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.content {
  width: 100%;
  min-height: 100%;
  min-width: 1000px;
  background-color: white;
  padding: 36px 40px;
}

.content-title {
  width: 100%; //min-width: 1160px;
}

.histroyapply-icon {
  width: 15px;
  height: 15px;
  background: url('../../images/histroyapply_icon.png') no-repeat;
  display: inline-block;
  float: left;
  margin: 2px 6px 0px 0px;
}

.historyapply-group {
  width: 100%; //min-width: 1000px;
  border: 1px solid #E4E4E4;
  padding: 18px 0 16px;
}

.historyapply-group-total {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 35px;
}

.historyapply-group-tablebox {
  // padding: 0px 18px 0px 14px;
  padding: 0px 50px;
  min-height: 440px;
}

.table-normal-box {
  min-height: 440px;
}

.pagination-normal {
  text-align: center;
  margin-top: 20px;
}

.content-title .header-right-time {
  margin-right: 10px;
}

.header-right-time {
  // margin-right: 10px;
  position: relative;
}

.header-right-time:nth-child(2) {
  margin-right: 0px;
}

.creditCz-box span:hover {
  cursor: pointer;
  color: #3C98E8;
}

.creditCz-box span:nth-child(2) {
  //padding: 0 10px;
}

.btn-cancle {
  border: 1px solid #3C98E8;
  color: #3C98E8;
  margin-right: 20px;
}

.form-group-inputbox {
  width: 470px;
  margin-right: 0px;
}

</style>



